body{
    height: 300vh;
    margin: 0;
    padding: 0;
}
.navbar_container{
    border: 1px solid black;
    height: 70px;
    background-color: rgb(0, 0, 0);
    position: fixed;
    top: 0;
    width: 100%;
    display: flex; 
}

.navbar_div{
    display: flex;
    /* border: 1px solid white; */
    width:45%;
    color: white;
    align-items: center;
    justify-content:space-between;
    cursor: pointer;
    margin: auto;
    margin-left:40%;
}

.navbar_div>h3{
    font-weight:300;
    font-family: 'Jost', sans-serif;
}

.navbar_div>h3:hover{
    color:#ffde59
}

@media only screen and (max-width:700px) {
    .navbar_div {
       width:97%;
       margin:auto;
    }
    .navbar_div>h3{
        font-weight:100;
        font-size:13px ;
    }
  }